@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body {
  background-color: #929293;
}

.gridlist {
  width: 100%;
  flex-wrap: wrap;
}
.paper {
  padding-top: 90px;
  padding-bottom: 75px;
  padding-left: 10px;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  height: 100%;
}

.tiles {
  box-shadow: 0.5px 0.5px 0.5px 0.5px;
  color: #d6d6d6;
  border-radius: 5px 5px 5px 5px;
}

.dialogtitle {
  font-size: 10px;
  font-family: 'Roboto', sans-serif;
  background-color: #f94722;
  box-shadow: 0px 0px 2px 0px;
}
.dialogtext {
  padding-top: 5px;
  font-size: 12px;
  color: #929293;
}
.tagline {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}
.tilebar {
  font-size: 12px;
}

.loadButton {
  margin-top: 10px;
  margin-bottom: 10px;
  box-shadow: 1px 1px 2px 1px #b3b3b3;
  background-color: #f94722;
  border-radius:14px;
  display:inline-block;
  cursor:pointer;
  color: black;
  font-family:Arial;
  font-size:19px;
  padding:18px 31px;
  text-decoration:none;
}
.loadButton:hover {
  background-color:#fc5b3a;
}
.loadButton:active {
  position:relative;
  top:1px;
}

        


.appbar {
	background-color: #f94722;
	height: 85px;
	text-align: center;
	font-size: 42px;
}
.logo {
	padding-top: 5px;
	padding-bottom: 5px;
	padding-right: 10px;
}
.footer {
	position: fixed;
	height: 70px;
	left: 0;
	bottom: 0;
	width: 100%;
	background-color: #f94722;
	text-align: center;
	box-shadow: 5px 5px 5px 5px; 
}
.logo {
	padding-top: 5px;
}
